import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import sku from '@/components/sku/pages/sku.vue'
// import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default{
	components:{
		uniRate,
		uniGoodsNav,
		sku,
		// uniPopup
	},
	onShow(){
		
	},
	data () {
	      return {
			  logined:false,
			  popshow:false,
			  uid:'',
			  token:'',
			  id:'',
			  merchId:'',
			  data:'',
			  thumb_url:'',
			  category:[],
			  total:'',
			  receivedCategory:false,
			  choosedSpec:'',
	        options: [{
	          icon: 'icon-kefu',
	          text: '客服'
	        }, {
	          icon: 'icon-iconfontshop-copy',
	          text: '店铺'
	        }, {
	          icon: 'icon-star',
	          text: '收藏',
			  collected:false,
			  color:'ff9c00'
	          // info: 0
	        }],
	        buttonGroup: [{
	          text: '加入购物车',
	          backgroundColor: '#ff0000',
	          color: '#fff'
	        },
	        {
	          text: '立即抢购',
	          backgroundColor: '#ffa200',
	          color: '#fff'
	        }
	        ]
	      }
	    },
		onLoad(p){
			var that=this
			this.id=p.id
			this.merchId=p.merchId
			var userInfo=uni.getStorageSync('userInfo')
			if(userInfo!='' & userInfo!=null & userInfo!=undefined){
				this.logined=true
				this.uid=userInfo.uid
				this.token=userInfo.token
			}else{
				this.logined=false
			}
			this.getDetail()
			uni.$on('logined',function(){
				var userInfo2=uni.getStorageSync('userInfo')
				that.logined=true
				that.uid=userInfo2.uid
				that.token=userInfo2.token
				that.getDetail()
			})
		},
	    methods: {
	      onClick (e) {
			  console.log(e)
			  if(e.index==0){
				  // 客服
			  }else if(e.index==1){
				  // 店铺
			  }else if(e.index==2){
				  // 收藏
				  this.collect(e.content.collected)
			  }
	        // uni.showToast({
	        //   title: `点击${e.content.text}`,
	        //   icon: 'none'
	        // })
	      },
	      buttonClick (e) {
	        console.log(e)
			if(e.index==0){
				this.addCollect()
			}else{
				this.buy()
			}
	        // this.options[2].info++
	      },
		  // 添加购物车
		  addCollect(){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  var that=this
			  var params={
			    uid:this.uid,
			    token: this.token,
				goodsid:this.id
			  }
			    var url='&r=api.member.cart.add'
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					that.$msg('添加成功')
			    })
		  },
		  // 购买
		  buy(){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  if(this.choosedSpec.choosedid=='' || this.choosedSpec.choosedid==undefined || this.choosedSpec.choosedid==null){
			  	this.chooseCategory()
			  	return ;
			  }
			  uni.navigateTo({
			  	url:'./createOrder?goodsId='+this.id+'&merchId='+this.merchId+'&choosedSpec='+JSON.stringify(this.choosedSpec)+'&goodsName='+this.data.title
			  })
		  },
		  // 购物车数量
		  getCollectNum(){
			  
		  },
		  // 更改收藏状态
		  collect(colected){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  var that=this,url
			  var params={
			    uid:this.uid,
			    token: this.token,
			  					goodsid:this.id
			  }
			  if(colected){
				   url='&r=api.member.favorite.remove'
			  }else{
				   url='&r=api.member.favorite.add'
			  }
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					if(colected){
						that.options[2].collected=false
						that.options[2].icon='icon-star'
					}else{
						that.options[2].collected=true
						that.options[2].icon='icon-shoucang'
					}
			    })
		  },
		  // 关注
		  focus(id){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  if(this.data.isCollect=='0'){
				  return ;
			  }
			  var that=this
			  var params={
			    uid:this.uid,
			    token: this.token,
			  	merchid:id
			  }
			    var url='&r=api.member.collection.add'
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					that.data.isCollect='0'
			  						that.$msg('已关注')
			    })
		  },
		  chooseCategory(){
			  this.$refs.sku.specClass='show'
		  },
		  openShare(){
			  this.popshow=true
		  },
		  getDetail(){
			  var that=this
			  var url='&r=api.goods.detail&id='+this.id+'&uid='+this.uid+'&token='+this.token
			  // var params={
			  //   page:p,
			  //   pagesize: this.pageSize
			  // }
			    // var url='/wangtosale_list'
			    this.$apiPost(url).then((res) =>{
					that.data=res.data
					that.thumb_url=res.data.thumb_url
					if(that.data.isFavorite=='1'){
							that.options[2].collected=false
							that.options[2].icon='icon-star'
					}else{
						that.options[2].collected=true
						that.options[2].icon='icon-shoucang'
					}
					that.$nextTick(function(){
						that.getCategory()
					})
			  	  // that.total=res.total
			  	  // that.dataList=that.dataList.concat(res.data)
			  	  // that.more=''
			    })
		  },
		  getCategory(){
			  var that=this
			  var url='&r=api.goods.detail.sku&goodsid='+this.id
			    this.$apiPost(url).then((res) =>{
					if(res.data!=''){
						that.category=res.data
						that.total=res.stock
						that.$nextTick(function(){
							that.receivedCategory=true
						})
					}
			  		
			    })
		  },
		  completeSpecChoose(e){
			  console.log(e)
			  this.choosedSpec=e
		  },
		  toComments(){
			  uni.navigateTo({
			  	url:'./goodsComments?id='+this.id
			  })
		  }
	    }
}
